import axios from 'axios';

let startpoint = "https://ctd-backend.onrender.com";

export const requestFormFun = (formData, setSuccessSubmit) => {
    axios.post(`${startpoint}/form_request`, formData)  // backend endpoint
        .then(res => {
            console.log(res.data, "Form submitted successfully");
            setSuccessSubmit(true);
        })
        .catch(err => {
            console.log(err, "Error in form submission");
            setSuccessSubmit(false);
        })
}



export const getServicesContentFun = async () => {
    try {
        const res = await axios.get(`${startpoint}/services_content`);  // backend endpoint
        return res.data;  // Return the fetched data
    } catch (err) {
        console.error('Error fetching data:', err);
        throw err;  // You could throw an error or return null
    }
}

