import React, { useEffect, useState } from "react";
import logo from '../assets/img/logo.png'
import { Link } from "react-router-dom";
// import "./Header.css";

const Header = () => {

    const handleMouseLeave = (event) => {
        if (event.currentTarget) {
            const parentDiv = event.currentTarget;
            const childDiv = parentDiv.querySelector(".dropdown-menu"); // Target the child div
            childDiv.classList.remove("show");
        }
    };

    return (
        <>
            <section className="top_bar_bg">
                <div className="container">
                    <div className="d-flex text-white justify-content-between align-items-center">
                        <div className="d-none d-md-flex">
                            <p className="m-0  my-2"><span className="icon-music"></span>We have Answers</p>
                            <ul className="d-flex m-0 my-2 list-unstyled">
                                <li className="mx-2" ><a className="text-white text-decoration-none" href="tel:+91-638 748 7660">+91-638 748 7660</a></li>
                                <span className="border-end"></span>
                                <li className="mx-2"><a className="text-white text-decoration-none" href="mailto:info@creativethinkdesign.com">info@creativethinkdesign.com</a></li>
                                <li></li>
                            </ul>
                        </div>
                        <div className="d-flex">
                            <ul className="d-flex m-0 my-2 list-unstyled">
                                <li className="mx-2"><Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="/about" className="text-white text-decoration-none" >About</Link></li>
                                <li className="mx-2"><Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to='/portfolio' className="text-white text-decoration-none">Portfolio</Link></li>
                                <li className="mx-2"><Link className="text-white text-decoration-none">Blogs</Link></li>
                                <li className="ms-2"><Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="/contact" className="text-white text-decoration-none">Contact</Link></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section>

            <header className="shadow-sm bg-white position-sticky top-0">
                <div className="container">
                    <nav className="navbar navbar-expand-lg d-flex justify-content-between">
                        <div className="row dis_contents">

                            <Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="/" className="navbar-brand w-auto" href="#home">
                                <img width={200} src={logo} alt="creative think design logo" />
                            </Link>
                            <button
                                className="navbar-toggler w-auto me-2"
                                type="button"
                                data-bs-toggle="collapse"
                                data-bs-target="#navbarNavDropdown"
                                aria-controls="navbarNavDropdown"
                                aria-expanded="false"
                                aria-label="Toggle navigation"
                            >
                                <span className="navbar-toggler-icon"></span>
                            </button>
                            <div className="w-auto collapse navbar-collapse mt-4" id="navbarNavDropdown">
                                <ul className="navbar-nav ms-auto">
                                    <li className="nav-item dropdown"
                                        onMouseLeave={handleMouseLeave}
                                    >
                                        <Link
                                            className="nav-link dropdown-toggle"
                                            href="#services"
                                            id="navbarDropdownMenuLink"
                                            role="button"
                                            data-bs-toggle="dropdown"
                                            aria-expanded="false"
                                        >
                                            UI/UX Design
                                        </Link>
                                        <ul className="dropdown-menu border-0 shadow-sm">
                                            <li>
                                                <Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="/webistedesign" className="dropdown-item py-2 my-1 bg-white" href="#web-design">
                                                    Website Design
                                                </Link>
                                            </li>

                                            <li>
                                                <Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="/uiapp" className="dropdown-item py-2 my-1 bg-white" href="#graphic-design">
                                                    UI App Design
                                                </Link>
                                            </li>
                                            <li className="dropdown-submenu">
                                                <Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="psdhtml" className="dropdown-item py-2 my-1 bg-white dropdown-toggle"
                                                    href="#development"
                                                >
                                                    PSD to HTML
                                                </Link>

                                            </li>
                                        </ul>
                                    </li>
                                    <li className="nav-item dropdown"
                                        onMouseLeave={handleMouseLeave}>
                                        <Link
                                            className="nav-link dropdown-toggle"
                                            href="#services"
                                            id="navbarDropdownMenuLink"
                                            role="button"
                                            data-bs-toggle="dropdown"
                                            aria-expanded="false"
                                        >
                                            Development
                                        </Link>
                                        <ul className="dropdown-menu border-0 shadow-sm">
                                            <li>
                                                <Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="/webdevelopment" className="dropdown-item py-2 my-1 bg-white" href="#web-design">
                                                    Website Development
                                                </Link>
                                            </li>
                                            <li>
                                                <Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="androidapp" className="dropdown-item py-2 my-1 bg-white" href="#graphic-design">
                                                    Android App Development
                                                </Link>
                                            </li>
                                            <li className="dropdown-submenu">
                                                <Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="/iosapp" className="dropdown-item py-2 my-1 bg-white dropdown-toggle"
                                                    href="#development"
                                                >
                                                    IOS App Development
                                                </Link>

                                            </li>
                                        </ul>
                                    </li>

                                    <li className="nav-item dropdown"
                                        onMouseLeave={handleMouseLeave}>
                                        <Link
                                            className="nav-link dropdown-toggle"
                                            href="#services"
                                            id="navbarDropdownMenuLink"
                                            role="button"
                                            data-bs-toggle="dropdown"
                                            aria-expanded="false"
                                        >
                                            Digital Marketing
                                        </Link>
                                        <ul className="dropdown-menu border-0 shadow-sm">
                                            <li>
                                                <Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="ppclick" className="dropdown-item py-2 my-1 bg-white" href="#web-design">
                                                    Pay Per Click
                                                </Link>
                                            </li>
                                            <li>
                                                <Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="seopage" className="dropdown-item py-2 my-1 bg-white" href="#graphic-design">
                                                    Social Media Optimization
                                                </Link>
                                            </li>
                                        </ul>
                                    </li>


                                    {/* <li className="nav-item dropdown"
                                        onMouseLeave={handleMouseLeave}>
                                        <Link
                                            className="nav-link dropdown-toggle"
                                            href="#services"
                                            id="navbarDropdownMenuLink"
                                            role="button"
                                            data-bs-toggle="dropdown"
                                            aria-expanded="false"
                                        >
                                            Graphics
                                        </Link>
                                        <ul className="dropdown-menu border-0 shadow-sm">
                                            <li>
                                                <Link className="dropdown-item py-2 my-1 bg-white" href="#web-design">
                                                    Packaging Design
                                                </Link>
                                            </li>
                                            <li>
                                                <Link className="dropdown-item py-2 my-1 bg-white" href="#graphic-design">
                                                    Magazines & Books Design
                                                </Link>
                                            </li>
                                            <li>
                                                <Link className="dropdown-item py-2 my-1 bg-white" href="#graphic-design">
                                                    Brochures & Catalogues Design
                                                </Link>
                                            </li>
                                        </ul>
                                    </li> */}


                                    <li className="nav-item">
                                        <Link onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} to="otherservice" className="nav-link" href="#about">
                                            Others
                                        </Link>
                                    </li>

                                </ul>
                            </div>
                        </div>

                        <button className="py-2 px-3 fs-6 head_connrect border-0 text-white d-none d-md-block">Connect with Us</button>


                    </nav>
                </div>


            </header>
        </>
    );
};

export default Header;
