import React, { useEffect, useState } from 'react';

import BannerComp from '../BannerComp';
import { getotherServiceFun } from '../../services/apis';

const OtherService = () => {

    const [otherData, setotherData] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            let getotherData = await getotherServiceFun();
            setotherData(getotherData);
        }
        fetchData()
    }, [])
 
    let breadcrumbtitle = "Other Services";

    return (
        <>
            <BannerComp breadcrumbtitle={breadcrumbtitle} />
            <section className='other_service_section'> 
                <div className="container">
                    <div className="row py-5">
                        <div className='col-12'>
                            <h1 className='h4 text-center pb-3'><b>{breadcrumbtitle}</b></h1>
                        </div>
                        {otherData.map((item, index) => {
                            return (
                                <div className="col-lg-4 col-sm-6" key={index}>
                                    <div className="item text-center p-4 mt-4 rounded-2 shadow-sm">
                                        <span className="icon fs-1 d-inline-block feature_box_col_one"><i className={item.icon_name}></i></span>
                                        <h1 className='fs-6 my-3'><b>{item.heading}</b></h1>
                                        <p>{item.paragraph}</p>
                                    </div>
                                </div>
                            )
                        })
                        }

                    </div>
                </div>
            </section>
        </>
    );
};


export default OtherService;