import React, { use, useEffect, useState } from 'react';

import './uiux.css'
import BannerComp from '../BannerComp';
import RequestForm from '../RequestForm'; 

const WebsiteComp = (props) => {

    const moduledata = props.moduledata;
    const modulename = props.modulename;

    let storedData = localStorage.getItem('finalShowDatalocal');

    const [userData, setUserData] = useState(() => {
        return storedData ? JSON.parse(storedData) : null;
    });


    useEffect(() => {
        moduledata.filter((item) => {
            if (item.breadcrumb === modulename) {
                setUserData(item);
                localStorage.setItem('finalShowDatalocal', JSON.stringify(item));
            }
        });
    }, [modulename])

    let finalfilteredData = storedData ? userData : userData ? userData : null;

    console.log(finalfilteredData, "finalfilteredData")


    return (
        <>
            <BannerComp breadcrumbtitle={finalfilteredData?.breadcrumb} />

            <div className="container"> 
                <div className="row mt-5">
                    <div className="col-md-8">
                        <div className="single-post-info-content mb-3">
                            <h1 className="fs-3 service-title"><b>{finalfilteredData?.heading_one}</b></h1>
                            <p className='m-0 p-0'>{finalfilteredData?.paragraph_one}</p>
                            <img className='w-100 my-3' src={finalfilteredData?.image_one} alt='Planning diagram image' />

                            <p className='m-0 p-0'> {finalfilteredData?.paragraph_two}</p>
                        </div>
                    </div>
                    <div className="col-md-4 col-xs-12 desk">
                        <RequestForm />
                    </div>
                    <div className="col-md-12">
                        <div className="row my-4 py-3 border mx-0">
                            <div className="col-md-6 col-xs-12">
                                <img className='w-100' src={finalfilteredData?.image_two} alt={`${finalfilteredData?.heading_one} Image`} />
                            </div>
                            <div className="col-md-6 col-xs-12">
                                <div className="single-post-info-content">
                                    <h3 className="fs-4 service-title"><b>{finalfilteredData?.heading_two}</b></h3>
                                    <p>{finalfilteredData?.paragraph_three}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-12">
                        <div className="otherswervice">
                            <p>{finalfilteredData?.paragraph_four}</p>

                            {finalfilteredData?.service_heading ?
                                <>
                                    <h2 className='fs-4'><b>{finalfilteredData?.service_heading} </b></h2>
                                    <ul className='list-unstyled'>

                                        {finalfilteredData?.service_list.map((item, index) => {
                                            return (
                                                <li className='mb-2 ms-2' key={index}><i className="fa fa-circle-o me-2" aria-hidden="true"></i> {item}</li>
                                            )
                                        })}
                                    </ul>
                                </> :

                                ""}
                            {finalfilteredData?.service_heading_two ?
                                <>
                                    <h2 className='fs-4'><b>{finalfilteredData?.service_heading_two}</b></h2>
                                    <ul className='list-unstyled'>
                                        {finalfilteredData?.service_list_two.map((item, index) => {
                                            return (
                                                <li className='mb-2 ms-2' key={index}><i className="fa fa-circle-o me-2" aria-hidden="true"></i> {item}</li>
                                            )
                                        })}

                                    </ul>
                                </>
                                : ""}


                        </div>

                    </div>

                </div>
            </div>
        </>
    );
};

export default WebsiteComp;