import React from 'react';
import BannerComp from '../BannerComp';

const PortfolioComp = () => {

    let breadcrumbtitle = "Portfolio";
    return (
        <>
        <BannerComp breadcrumbtitle={breadcrumbtitle} />
        <section className="bg-white">
        <div className="container py-4">
            <div className="row ">
                <div className="col-12 mb-4">
                    <div className="d-flex justify-content-between align-items-center">
                        <div className="sec-title">
                            <p className="m-0">Fabulous </p>
                            <div className="title"><h2><b>Our team Worked</b></h2></div>
                        </div>
                        <div className="more-portfolio">
                            <a className="btn-two text-decoration-none d-flex" target='_blank' href="https://www.behance.net/creativethinkdesign">View More <i className="fa fa-long-arrow-right fs-5 ms-2" aria-hidden="true"></i> </a>
                        </div>
                    </div>

                </div>
            </div>
            <div className="row">
                <div className="col-md-6 mt-3 col-xs-12">
                    <div className="h-100 bg-white portd">
                        <img className="port w-100" src="https://www.creativethinkdesign.com/images/portfolio/vishnu.png" alt="portfolio image" />
                        <div className="portxt p-3">
                            <h5 className="m-0 mb-1">Business Ads Banner</h5>
                            <p className="m-0">Advertising is the best way to communicate to the customers.</p>
                        </div>
                    </div>
                </div>
                <div className="col-md-6 mt-3 col-xs-12">
                    <div className="h-100 bg-white portd">
                        <img className="port w-100" src="https://www.creativethinkdesign.com/images/portfolio/app.png" alt="portfolio image" />
                        <div className="portxt p-3">
                            <h5 className="m-0 mb-1">Mobile Apps Design</h5>
                            <p className="m-0">Mobile apps are the best platform for your business as it provides all the relevant information related to your business and brand.</p>
                        </div>
                    </div>
                </div>
                <div className="col-md-6 mt-3 col-xs-12">
                    <div className="h-100 bg-white portd">
                        <img className="port w-100" src="https://www.creativethinkdesign.com/images/portfolio/website.png" alt="portfolio image" />
                        <div className="portxt p-3">
                            <h5 className="m-0 mb-1">Website Design</h5>
                            <p className="m-0">Your company's website can be a highly effective marketing tool that can be very cost-efficient.</p>
                        </div>
                    </div>
                </div>
                <div className="col-md-6 mt-3 col-xs-12">
                    <div className="h-100 bg-white portd">
                        <img className="port w-100" src="https://www.creativethinkdesign.com/images/portfolio/broc.png" alt="portfolio image" />
                        <div className="portxt p-3">
                            <h5 className="m-0 mb-1">Brouchure Design</h5>
                            <p className="m-0">A well designed brochure serves as a perfect introduction to your business.</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
        </>
    );
};

export default PortfolioComp;