import React from 'react';
import BannerComp from '../BannerComp';
import RequestForm from '../RequestForm';

const ContactUs = () => {

    let breadcrumbtitle = "Contact Us"
    return (
        <>
            <BannerComp breadcrumbtitle={breadcrumbtitle} />
            <section>
                <div className='container'>
                    <div className='row py-5'>
                        <div className='col-12'>
                            <div className=''>
                                <h1 className='fs-3 mb-2'><b>Have a Question or Need Our Help?</b></h1>
                                <p className='mb-2'>If you want to know more about our work, need a service, or need support.</p>
                                <h2 className='fs-5 mb-2'><b> just send us a message.</b></h2>
                                <p className='mb-2'>Just fill out the form and we'll get back to you within 24 hours.</p>
                            </div>
                        </div>



                        <div className='col-md-7 col-12 mt-3'>
                            <RequestForm />
                        </div>
                        <div className='col-md-5 col-12 mt-3'>
                            <div className="inner text-center">
                                <div className="single_info_box p-5 w-100 text-white">
                                    <div className="icon">
                                        <span><i className="fa fa-map-marker fs-3 d-inline-flex justify-content-center align-items-center border p-3 rounded-circle" ></i></span>
                                    </div>
                                    <div className="text">
                                        <h3 className='fs-5 mb-2 mt-3'><b>Address</b></h3>
                                        <p>	H/N-64 Karim Complex Ground Floor Near Buxipur, Gorakhpur-273001, UP India</p>
                                    </div>
                                </div>
                                <div className="single_info_box single_info_box_two text-white p-5 w-100">
                                    <div className="icon">
                                        <span><i className="fa fa-headphones fs-3 d-inline-flex justify-content-center align-items-center border p-3 rounded-circle" ></i></span>
                                    </div>
                                    <div className="text">
                                        <h3 className='fs-5 mb-2 mt-3'><b>Contact Details</b></h3>
                                        <p className='m-0 mb-2'><a className="text-white text-decoration-none" href="mailto:info@creativethinkdesign.com">info@creativethinkdesign.com</a></p>
                                        <p className='m-0'><a className="text-white text-decoration-none" href="tel:+91-638 748 7660">+91-638 748 7660</a></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className='col-12'>
                            <p className='mt-5 fs-3 cursive_text letconnect text-center'>Lets connect and see how we can help!</p>
                        </div>
                    </div>

                </div>
            </section>
        </>
    );
};

export default ContactUs;