
import Header from './components/Header';
import React, { useEffect } from 'react';

import {
  Route,
  Routes
} from "react-router-dom";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min.js";
import './App.css'; 
import '../src/components/other/others.css'; 

import { getServicesContentFun } from './services/apis';
import Home from './components/home/Home';
import Footer from './components/Footer'; 
import WebsiteComp from './components/menucomp/WebsiteComp';
import OtherService from './components/other/otherService';
import ContactUs from './components/other/ContactUs';
import AboutUs from './components/other/AboutUs';
import PortfolioComp from './components/other/PortfolioComp';
 

function App() {


  const [serviceContent, setserviceContent] = React.useState([]);

  // console.log(serviceContent, "users")


  useEffect(() => {
    const fetchData = async () => {
      try {
        const fetchedData = await getServicesContentFun();  // Call your function
        setserviceContent(fetchedData)
      } catch (err) {
        console.log('Failed to fetch data');
      }
    };

    fetchData();
  }, []);



  return (
    <div className="App">
      <Header />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/webistedesign" element={<WebsiteComp moduledata={serviceContent} modulename="Website Design" />} />
        <Route path="/uiapp" element={<WebsiteComp moduledata={serviceContent} modulename="iOS & Android App Design" />} />
        <Route path="/psdhtml" element={<WebsiteComp moduledata={serviceContent} modulename="PSD to Html Conversion" />} />
        <Route path="/webdevelopment" element={<WebsiteComp moduledata={serviceContent} modulename="Website Development" />} />
        <Route path="/androidapp" element={<WebsiteComp moduledata={serviceContent} modulename="Android App Development" />} />
        <Route path="/iosapp" element={<WebsiteComp moduledata={serviceContent} modulename="iOS App Development" />} />
        <Route path="/ppclick" element={<WebsiteComp moduledata={serviceContent} modulename="Pay Per Click" />} />
        <Route path="/seopage" element={<WebsiteComp moduledata={serviceContent} modulename="Social Media Optimization" />} />
    
        <Route path="/portfolio" element={<PortfolioComp />} />
        <Route path="/otherservice" element={<OtherService />} />
        <Route path="/about" element={<AboutUs />} />
        <Route path="/contact" element={<ContactUs />} />
      </Routes>
      <Footer />
    </div>
  );
}

export default App;
