import React, { useState } from "react";
import Slider from "react-slick";

const SlidersComp = (props) => {
    const [activeIndex, setActiveIndex] = useState(0);

    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        
        initialSlide: 0,
 
        autoplay: true,
        autoplaySpeed: 2000, // in milliseconds (2 seconds)

        infinite: true,       // usually needed for autoplay
        pauseOnHover: true,   // optional: pauses autoplay on hover

        centerMode: true,
        centerPadding: "0px",
        slidesToScroll: 1,    // Must be 1 for centerMode

        infinite: true,
        beforeChange: (oldIndex, newIndex) => setActiveIndex(newIndex),

        responsive: [
            {
                breakpoint: 767,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };



    let items = [1, 2, 3, 4, 5]

    return (
        <div className="slider-container">

            <Slider {...settings}>

                {props.items.map((item, index) => (
                    <div key={item}>
                        <div
                            style={{
                                background: activeIndex === index ? "#3498db" : "#ccc",
                                padding: "40px",
                                textAlign: "center",
                                opacity: activeIndex === index ? 1 : 0.5,
                                transition: "0.3s"
                            }}
                        >
                            <div className="overlay-content text-center position-relative">
                                <div className="img-box">
                                    <img width={80} height={80} className="rounded m-auto mb-3" src={item.imgurl} alt={`${item.name} image`} />
                                </div>
                                <div className="text-box">
                                    <p>This is Best Company with an respected People!  His work is awesome so between awesome work and customer service.</p>
                                    <div className="quote-icon">
                                        <i className="fa fa-quote-left position-absolute top-50 left-50" aria-hidden="true"></i>

                                    </div>
                                </div>
                                <div className="client-info">
                                    <div className="review-box">
                                        <ul className="list-unstyled d-flex justify-content-center">
                                            <li><i className="fa fa-star fs-6 mx-1"></i></li>
                                            <li><i className="fa fa-star fs-6 mx-1"></i></li>
                                            <li><i className="fa fa-star fs-6 mx-1"></i></li>
                                            <li><i className="fa fa-star fs-6 mx-1"></i></li>
                                            <li><i className="fa fa-star fs-6 mx-1"></i></li>
                                        </ul>
                                    </div>
                                    <h3 className="fs-6"><b>{item.name}</b></h3>
                                </div>
                            </div>
                        </div>
                    </div>
                ))}
            </Slider>
        </div>
    );
}
export default SlidersComp;