import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css'; // Import Bootstrap CSS
import axios from 'axios';
import { requestFormFun } from '../services/apis';

const RequestForm = () => {
    const [formData, setFormData] = useState({
        name: '',
        number: '',
        email: '',
        message: '',
    });


    const [errors, setErrors] = useState({});

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });

        // Clear the error for the field being updated
        setErrors((prevErrors) => ({
            ...prevErrors,
            [name]: '',
        }));
    };

    const validate = () => {
        const newErrors = {};

        // Name validation
        if (!formData.name.trim()) {
            newErrors.name = 'Name is required';
        } else if(formData.name.trim().length<3){
            newErrors.name = 'Name must be more than 2 characters.';
        }

        // Phone validation
        if (!formData.number.trim()) {
            newErrors.number = 'Phone number is required';
        } else if (!/^\d{10}$/.test(formData.number)) {
            newErrors.number = 'Valid 10-digit phone number is required';
        }

        // Email validation
        if (!formData.email.trim()) {
            newErrors.email = 'Email is required';
        } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
            newErrors.email = 'Valid email is required';
        }

        // Request validation
        if (!formData.message.trim()) {
            newErrors.message = 'Request is required';
        }

        return newErrors;
    };

    const [successsubmit, setSuccessSubmit] = useState(false);

    setTimeout(() => {
        setSuccessSubmit(false); 
    }, 5000); // 5 seconds

    const handleSubmit = (e) => {  

        e.preventDefault();
        const validationErrors = validate();

        // Always set errors, even if the form is invalid
        setErrors(validationErrors);

        if (Object.keys(validationErrors).length === 0) {
            console.log(formData, "formData");
            requestFormFun(formData, setSuccessSubmit);
            setErrors({});
            // Add form submission logic here
        }
    };


    return (
        <div className="card">
            <div className="card-body">
                <h1 className="fs-5 card-title mb-4"><b>Send us your Requirement</b></h1>
                <form onSubmit={handleSubmit}>
                    <div className="mb-3">
                        <label htmlFor="name" className="form-label position-absolute invisible">Name:</label>
                        <input
                            type="text"
                            id="name"
                            autoComplete="text"
                            name="name"
                            className={`form-control p-3 rounded-0 ${errors.name ? 'is-invalid' : ''}`}
                            value={formData.name}
                            onChange={handleChange}
                            placeholder="Name"
                        />
                        {errors.name && <div className="invalid-feedback">{errors.name}</div>}
                    </div>
                    <div className="mb-3">
                        <label htmlFor="number" className="form-label position-absolute invisible">Phone:</label>
                        <input
                            type="number"
                            id="number"
                            autoComplete="number"
                            name="number"
                            className={`form-control p-3 rounded-0 ${errors.number ? 'is-invalid' : ''}`}
                            value={formData.number}
                            onChange={handleChange}
                            placeholder="Phone"
                        />
                        {errors.number && <div className="invalid-feedback">{errors.number}</div>}
                    </div>
                    <div className="mb-3">
                        <label htmlFor="email" className="form-label position-absolute invisible">Email:</label>
                        <input
                            type="email"
                            id="email"
                            autoComplete="email"
                            name="email"
                            className={`form-control p-3 rounded-0 ${errors.email ? 'is-invalid' : ''}`}
                            value={formData.email}
                            onChange={handleChange}
                            placeholder="Email"
                        />
                        {errors.email && <div className="invalid-feedback">{errors.email}</div>}
                    </div>
                    <div className="mb-3">
                        <label htmlFor="message" className="form-label position-absolute invisible">Request:</label>
                        <textarea
                            id="message"
                            name="message"
                            className={`form-control p-3 rounded-0 ${errors.message ? 'is-invalid' : ''}`}
                            value={formData.message}
                            onChange={handleChange}
                            placeholder="Enter your special request"
                            rows="4"
                        />
                        {errors.message && <div className="invalid-feedback">{errors.message}</div>}
                    </div>
                    <div className="d-grid submit_btn">
                        <button type="submit" className="btn btn-one p-3 rounded-0">Send Request</button>
                    </div>
                </form>
            </div>
            {successsubmit && <div className="alert successpopup position-fixed top-0 end-0 alert-success shadow-lg">Form submitted successfully!</div>}
             
        </div>
    );
};

export default RequestForm;