import React from 'react';
import supportbg from '../assets/img/supportbg.jpg';
import { Link } from 'react-router-dom';


const BannerComp = (props) => {
    return (
        <section>
            <div className="support_bgimage position-relative m-0">
                <img className="w-100" alt="support team background image" src={supportbg} />
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="pro_teams start-0 end-0 position-absolute top-50 text-center text-white">
                                <div className="title mb-md-3 mb-3">
                                    <h2><b>{props.breadcrumbtitle}</b></h2>
                                    <p>Were here to solve your problems.</p>
                                </div>
                            </div>
                            <div className="breadcrumb_menu position-absolute">
                                <ul className='list-unstyled d-flex m-0 p-0'>
                                    <li><Link to="/" className='text-decoration-none'>Home  </Link></li>
                                    <li className="active"> <span className='mx-1'>~</span>{props.breadcrumbtitle}</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default BannerComp;